import { Query } from "react-apollo";
import gql from "graphql-tag";

const ALL_ORDERS_QUERY = gql`
  query ALL_ORDERS_QUERY {
    orders {
      id
      total
      createdAt
      items {
        image
      }
    }
  }
`;

const Orders = (props) => {
  return (
    <Query query={ALL_ORDERS_QUERY}>
      {({ data, loading, error }) => {
        console.log(data);
        return <p>poo</p>;
      }}
    </Query>
  );
};

export default Orders;
